




























































































import Vue from "vue";
import AccountBase from "@/components/account/AccountBase.vue";
import { messages, rules } from "@/constants";
import AccountServiec from "@/api/service/account.service";
import { apiCall } from "@/api";
export default Vue.extend({
  data: () => ({
    signUpModel: {
      userName: "",
      fullName: "",
      mobileNo: "",
      email: "",
      password: "",
    },
    rules: rules,
    show: false,
    accountService: new AccountServiec(apiCall),
  }),
  components: {
    AccountBase,
  },
  methods: {
    register() {
      let isValid = this.$refs.signUpForm.validate();
      if (isValid) {
        this.$root.$refs.loading.open();
        this.accountService
          .SignUp({
            userName: this.signUpModel.userName,
            fullName: this.signUpModel.userName,
            password: this.signUpModel.password,
            email: this.signUpModel.email,
            mobileNo: this.signUpModel.mobileNo,
            application: { apikey: "", password: "" },
          })
          .then((res) => {
            if (res.status)
              this.$router.push({
                name: "Login",
                query: { userName: this.signUpModel.userName },
              });
            this.showMessage(res.title);
          })
          .catch((e) => {
            this.showMessage(messages.netWorkError(e.message).title);
          });
      } else this.showMessage(messages.invalidForm);
    },
    showMessage(text: string) {
      this.$root.$refs.snackbar.open(text);
      this.$root.$refs.loading.close();
    },
  },
});
